.direction {
  border: 1px solid #cdcdcd;
  position: relative;
  width: 318px;
  padding: 0;
  margin: 0 auto;
  margin-bottom: 29px;
}
.direction__wrapper {
  padding: 0 19px;
  width: 280px;
  padding-top: 25px;
  padding-bottom: 3px;
}
.direction__wrapper label {
  position: relative;
  cursor: pointer;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: 400;
}
.direction__wrapper label::before,
.direction__wrapper label::after {
  content: "";
  position: absolute;
  top: -4px;
  left: -45px;
  border-radius: 50%;
}
.direction__wrapper label::before {
  width: 30px;
  height: 30px;
  border: 1px solid #e6e6e6;
}
.direction__item:hover label::before {
  border-color: #cdcdcd;
}
.direction__item:active label {
  opacity: 0.3;
}
.direction__wrapper label::after {
  position: absolute;
  top: 6px;
  left: -35px;
  width: 12px;
  height: 12px;
  background-color: #ffffff;
}
.direction__item {
  padding-bottom: 22px;
  padding-left: 45px;
}
.direction__wrapper input[type="radio"] {
  display: none;
}
input[type="radio"]:checked ~ label::after {
  background-color: #68b738;
}
